.vaccine-app-note {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  color: #333;
  font-size: 0.3rem;
}

.vaccine-app-note .row-wrapper-first {
  padding: 0;
  height: 0.6rem;
  line-height: 0.6rem;
  background-color: #ccf0e2;
  color: #ff7043;
  text-align: center;
}

.vaccine-app-note .row-wrapper::after {
  clear: both;
  content: '';
  display: block;
  width: 0;
  height: 0;
  visibility: hidden;
}

.vaccine-app-note .row-wrapper {
  width: 100%;
  padding: 0.3rem;
  margin-bottom: 0.2rem;
  background-color: #fff;
  font-size: 0.3rem;
  box-sizing: border-box;
}

.vaccine-app-note .row-wrapper .row-title {
  width: 100%;
  font-size: 0.4rem;
  border-bottom: 0.01rem solid #e5e5e5;
}

.vaccine-app-note .row-wrapper .row-item {
  float: left;
  width: 100%;
  padding-top: 0.15rem;
  padding-bottom: 0.15rem;
}

.vaccine-app-note .row-wrapper .row-item .item-title {
  float: left;
}

.vaccine-app-note .row-wrapper .row-item .item-stage {
  float: left;
}

.vaccine-app-note .row-wrapper .row-item .item-icon {
  float: right;
}

.vaccine-app-note .row-wrapper .item-time {
  width: 100%;
  float: left;
  padding-top: 0.15rem;
  color: #999;
}

.vaccine-app-details .row-wrapper {
  width: 100%;
  padding: 0.3rem;
  background-color: #fff;
  font-size: 0.3rem;
  box-sizing: border-box;
}

.vaccine-app-details .row-wrapper .row-title {
  width: 100%;
  font-size: 0.4rem;
  border-bottom: 0.01rem solid #e5e5e5;
}

.vaccine-app-details .row-wrapper .sub-title {
  color: #333;
  position: relative;
  padding-top: 0.15rem;
  padding-bottom: 0.15rem;
}

.vaccine-app-details .row-wrapper .sub-title .row-mark {
  border-left: 0.1rem solid #06cc7e;
  height: 0.3rem;
  width: 0.04rem;
  position: absolute;
  left: -0.3rem;
  top: 0.2rem;
}

.vaccine-app-details .row-wrapper .row-content {
  color: #666;
  width: 100%;
  line-height: 0.6rem;
  white-space: normal;
  word-break: break-all;
}